import React, { FC, MouseEvent, PropsWithChildren } from "react"
import "./button.css"
interface IButtonProps {
	className?: string
	onClick?: (e: MouseEvent<HTMLButtonElement>) => void
	disabled?: boolean
}
const Button: FC<PropsWithChildren<IButtonProps>> = ({
	children,
	className,
	onClick,
	disabled,
}: PropsWithChildren<IButtonProps>): JSX.Element => {
	return (
		<button
			disabled={disabled}
			className={["btn", className].join(" ")}
			onClick={onClick}
		>
			{!children ? "Простая кнопка" : children}
		</button>
	)
}

export default Button
