import React, { useContext, useEffect } from 'react'
import MainFilter from './MainFilter'
import UserItem from './UserItem'
import { Guid } from 'guid-typescript'
import IRandomForm from '../../Home/IRandomForm'
import { TableContext } from '../TableForms'
const MainPanel = (): JSX.Element => {
	const { users, errorFetch, isLoading, usersForFilters } =
		useContext(TableContext)
	useEffect(() => {}, [users])
	return (
		<div className='main-wrapper'>
			<div className='container-fluid'>
				<div className='admin-heading-1'>Все заявки</div>

				<MainFilter />

				<table className='table fs-14'>
					<thead>
						<tr>
							<th>ID</th>
							<th>дата</th>
							<th>продукт</th>
							<th>имя</th>
							<th>email</th>
							<th>телефон</th>
							<th>статус</th>
							<th></th>
						</tr>
					</thead>
					<tbody id='tbody'>
						{usersForFilters.length > 0 &&
							usersForFilters.map(
								(value: IRandomForm): JSX.Element => (
									<UserItem key={Guid.raw()} value={value} />
								),
							)}
					</tbody>
				</table>
				{isLoading && <h2 className='user__loading'> Loading... </h2>}
				{errorFetch && <h2 className='user__error'>{errorFetch}</h2>}
				{!isLoading && users.length <= 0 && (
					<h2 className='user__no-find'> Пользователи не найдены </h2>
				)}
			</div>
		</div>
	)
}

export default MainPanel
