import React, { ChangeEvent, MouseEvent, useContext } from 'react'
import { EditContext } from './Edit'
const FormEdit = (): JSX.Element => {
	const { setFormFields, saveForm, form, deleteAccount, message } =
		useContext(EditContext)
	const { First_Name, date, email, id, status, telephone, typeProduct } = form

	return (
		<form
			id='form'
			onSubmit={(e: ChangeEvent<HTMLFormElement>) => {
				saveForm(e)
			}}>
			{message === '' && (
				<>
					<div className='card mb-4'>
						<div className='card-header'>Данные о заявке</div>
						<div className='card-body'>
							<div className='row mb-3'>
								<div className='col-md-2'>
									<strong>ID:</strong>
								</div>
								<div className='col'>
									Заявка №<span id='number'> {id} </span>
								</div>
								<input name='id' type='hidden' id='id' />
							</div>

							<div className='row mb-3'>
								<div className='col-md-2'>
									<strong>Дата создания:</strong>
								</div>
								<div className='col' id='date'>
									{date}
								</div>
							</div>

							<div className='row mb-3'>
								<div className='col-md-2'>
									<strong>Продукт:</strong>
								</div>
								<div className='col'>
									<select
										id='product'
										name='product'
										className='custom-select'
										defaultValue={typeProduct}
										onChange={(e: ChangeEvent<HTMLSelectElement>) => {
											setFormFields(e.target.name, e.target.value, e)
										}}>
										<option value='course-html'>Курс по верстке</option>
										<option value='course-js'>Курс по JavaScript</option>
										<option value='course-vue'>Курс по VUE JS</option>
										<option value='course-php'>Курс по PHP</option>
										<option value='course-wordpress'>Курс по WordPress</option>
									</select>
								</div>
							</div>

							<div className='row mb-3'>
								<div className='col-md-2'>
									<strong>Имя:</strong>
								</div>
								<div className='col'>
									<input
										type='text'
										className='form-control'
										defaultValue={First_Name}
										id='name'
										name='name'
										onChange={(e: ChangeEvent<HTMLInputElement>) => {
											setFormFields(e.target.name, e.target.value, e)
										}}
									/>
								</div>
							</div>

							<div className='row mb-3'>
								<div className='col-md-2'>
									<strong>Email:</strong>
								</div>
								<div className='col'>
									<input
										type='text'
										className='form-control'
										defaultValue={email}
										id='email'
										name='email'
										onChange={(e: ChangeEvent<HTMLInputElement>) => {
											setFormFields(e.target.name, e.target.value, e)
										}}
									/>
								</div>
							</div>

							<div className='row mb-3'>
								<div className='col-md-2'>
									<strong>Телефон:</strong>
								</div>
								<div className='col'>
									<input
										type='text'
										className='form-control'
										defaultValue={telephone}
										id='phone'
										name='phone'
										onChange={(e: ChangeEvent<HTMLInputElement>) => {
											setFormFields(e.target.name, e.target.value, e)
										}}
									/>
								</div>
							</div>

							<div className='row mb-3'>
								<div className='col-md-2'>
									<strong>Статус заявки:</strong>
								</div>
								<div className='col'>
									<select
										className='custom-select'
										id='status'
										name='status'
										defaultValue={status}
										onChange={(e: ChangeEvent<HTMLSelectElement>) => {
											setFormFields(e.target.name, e.target.value, e)
										}}>
										<option>Выберите...</option>
										<option value='new'>Новая</option>
										<option value='inwork'>В работе</option>
										<option value='complete'>Завершена</option>
									</select>
								</div>
							</div>
						</div>
					</div>
					<div className='row justify-content-between'>
						<div className='col text-right'>
							<button
								className='btn btn-secondary'
								onClick={(e: MouseEvent<HTMLButtonElement>) =>
									deleteAccount(id, e)
								}>
								Удалить
							</button>
							<button className='btn btn-primary'>Сохранить изменения</button>
						</div>
					</div>
				</>
			)}
			{message !== '' && <h2 className='danger'> {message} </h2>}
		</form>
	)
}

export default FormEdit
