import React, {
	ChangeEvent,
	Context,
	createContext,
	useEffect,
	useState,
} from 'react'
import Header from '../sections/Header/Header'
import Home from '../pages/Home/Home'
import randomForm from '../../../dataBase/randomInputs.json'
import IRandomForm from '../pages/Home/IRandomForm'
import IProps from '../pages/Home/IProps'
import useReadUser from '../CRUD/useReadUser'
import serverUrl from '../helpers/serverInfo'
import useCreateUser from '../CRUD/useCreateUser'
import { HashRouter as Router, Routes, Route } from 'react-router-dom'
import TableForms from '../pages/TableForms/TableForms'
import Edit from '../pages/Edit/Edit'

export const init: IRandomForm = {
	id: 0,
	date: '',
	status: '',
	First_Name: '',
	email: '',
	telephone: '',
	typeProduct: '',
}

export const AppContext: Context<IProps> = createContext(init)
const randomInputs: IProps[] = randomForm

const App = (): JSX.Element => {
	{
		;(' ')
	}
	const [randomForms, setRandomForms] = useState<IProps>(init)

	const [flagCreate, setFlagCreate] = useState<boolean>(false)

	const [users, setUsers] = useState<IRandomForm[]>([])

	const [sendError, setSendError] = useState<string>('')

	//  ============= Чтение из данных из БД=================================
	const { data, isLoading, errorFetch } = useReadUser<IRandomForm>(
		`${serverUrl}/users`,
		flagCreate,
	)

	useEffect(() => {
		setUsers(data)
	}, [data])

	// =============================================================

	// рандомное значение из файла для заполнения формы начальными значениями, один раз при загрузки страницы
	useEffect(() => {
		if (randomForm.length > 0) {
			const rand: number = Math.floor(Math.random() * randomInputs.length)
			setRandomForms({
				First_Name: randomInputs[rand].First_Name,
				telephone: randomInputs[rand].telephone,
				email: randomInputs[rand].email,
				typeProduct: randomInputs[rand].typeProduct,
				date: new Date().toLocaleDateString('ru-RU', {
					dateStyle: 'short',
				}),
				status: randomForm[rand].status,
			})
		}
	}, [flagCreate])

	// Функция изменения формы inputs и select
	const setFormFields = (name: string, value: string): void => {
		name === 'name' && setRandomForms({ ...randomForms, First_Name: value })
		name === 'email' && setRandomForms({ ...randomForms, email: value })
		name === 'phone' && setRandomForms({ ...randomForms, telephone: value })
		name === 'product' && setRandomForms({ ...randomForms, typeProduct: value })
	}

	// Отправляем данные в БД на сервер
	const sendForm = async (e: ChangeEvent<HTMLFormElement>) => {
		e.preventDefault()
		const { First_Name, email, telephone, date, status } = randomForms

		if (
			First_Name.trim() !== '' &&
			email.trim() !== '' &&
			telephone.trim() !== ''
		) {
			setRandomForms({
				id: users.length > 0 ? users[users.length - 1].id + 1 : 1,
				status: 'Новый',

				...randomForms,
			})

			const result = await useCreateUser(`${serverUrl}/users`, randomForms)

			result.ok && setFlagCreate(!flagCreate)

			setSendError('')
		} else {
			setSendError('Заполнены не все поля!')
		}
	}

	return (
		<AppContext.Provider
			value={{
				...randomForms,
				setFormFields,
				sendForm,
				sendError,
				users,
				isLoading,
				errorFetch,
				setFlagCreate,
			}}>
			<Router>
				<Header />
				<Routes>
					<Route path='/' element={<Home />} />
					<Route path='/table' element={<TableForms />} />
					<Route path='/users/edit/:id' element={<Edit />} />
				</Routes>
			</Router>
		</AppContext.Provider>
	)
}

export default App
