import React, {
	ChangeEvent,
	MouseEvent,
	createContext,
	useContext,
	useEffect,
	useState,
} from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import FormEdit from './FormEdit'
import IEditProps from './IEditProps'
import serverUrl from '../../helpers/serverInfo'
import useReadUser from '../../CRUD/useReadUser'
import IRandomForm from '../Home/IRandomForm'
import { AppContext, init } from '../../App/App'
import useEditUser from '../../CRUD/useEditUser'
import useDeleteUser from '../../CRUD/useDeleteUser'
export const EditContext = createContext<IEditProps>({})

const Edit = (): JSX.Element => {
	//
	const { id } = useParams()

	const { setFlagCreate, users, isLoading, errorFetch } = useContext(AppContext)

	// устанавливаем состояние для пользователей

	const [form, setForm] = useState<IRandomForm>(init)

	//===================================================================

	// Функция изменения формы inputs и select
	const user = users.length > 0 && users.find((user) => user.id === +id)
	useEffect(() => {
		setForm(user)
	}, [user])

	const setFormFields = (name: string, value: string, e: ChangeEvent): void => {
		name === 'name' && setForm({ ...form, First_Name: value })
		name === 'email' && setForm({ ...form, email: value })
		name === 'phone' && setForm({ ...form, telephone: value })
		name === 'product' && setForm({ ...form, typeProduct: value })
		name === 'status' && setForm({ ...form, status: value })
	}

	// ==================================================

	//=================== Отправка изменений на сервер ========================
	const [message, setMessage] = useState('')

	const navigate = useNavigate()
	const saveForm = async (e: ChangeEvent<HTMLFormElement>) => {
		e.preventDefault()
		const { First_Name, date, email, id, status, telephone, typeProduct } = form
		if (
			First_Name.trim() !== '' &&
			email.trim() !== '' &&
			telephone.trim() !== '' &&
			status.trim() !== ''
		) {
			const result = await useEditUser<IRandomForm>(
				`${serverUrl}/users/${id}`,
				form,
			)

			if (result.ok) {
				setMessage(
					'Ваши данные успешно обновлены! Сейчас Вас перенаправят на страницу с таблицами заявок. Пожалуйста, подождите...',
				)
				// Если успешное редактирование меняем флаг, для обновления состояния
				setFlagCreate((prev) => !prev)
				setTimeout(() => {
					//	  перенаправляем на страницу table
					navigate('/table')
				}, 5000)
			}
		}
	}

	// =========================================================================

	// ============== Удаление аккаунта ========================================

	const deleteAccount = async (
		id: number,
		e: MouseEvent<HTMLButtonElement>,
	) => {
		e.preventDefault()
		const result = await useDeleteUser(`${serverUrl}/users/${id}`)
		if (result.ok) {
			// Если успешное удаление меняем флаг, для обновления состояния
			setFlagCreate((prev) => !prev)
			//	  перенаправляем на страницу table
			navigate('/table')
		}
	}

	// =========================================================================

	return (
		<>
			{isLoading && <h2> Loading... </h2>}
			{errorFetch && <h2> {errorFetch} </h2>}
			<div className='form-wrapper'>
				<div className='container-fluid'>
					<div className='row justify-content-between align-items-center'>
						<div className='col'>
							<div className='admin-heading-1'>Работа с заявкой</div>
						</div>
						<div className='col text-right'>
							<Link to='/table' className='btn btn-link'>
								Вернуться назад
							</Link>
						</div>
					</div>

					<div className='row'>
						<div className='col'>
							<EditContext.Provider
								value={{
									...user,
									saveForm,
									setFormFields,
									form,
									deleteAccount,
									message,
								}}>
								<FormEdit />
							</EditContext.Provider>
						</div>
					</div>
				</div>
			</div>
		</>
	)
}

export default Edit
