import { useEffect, useState } from 'react'

const useReadUser = <T,>(url: string, flag?: boolean) => {
	const [data, setData] = useState<T[]>([])
	const [isLoading, setLoading] = useState<boolean>(true)
	const [errorFetch, setErrorFetch] = useState<string | null>(null)
	useEffect(() => {
		fetch(url)
			.then((res) => {
				if (!res.ok) {
					throw new Error('Не могу загрузить данные')
				} else {
					return res.json()
				}
			})
			.then((value) => {
				setData(value)
				setLoading(false)
				setErrorFetch(null)
			})
			.catch((err) => {
				setErrorFetch(err)
				setLoading(false)
			})
	}, [flag])
	return {
		data,
		isLoading,
		errorFetch,
	}
}

export default useReadUser
