import React, { useContext } from 'react'
import LogoLeftPanel from './LogoLeftPanel'
import UserAvatar from './UserAvatar'
import { TableContext } from '../TableForms'
import IRandomForm from '../../Home/IRandomForm'
import userStatus, { IStatus } from '../../../helpers/status'
import { Guid } from 'guid-typescript'
import translate from '../../../helpers/translate'
import { MouseEvent } from 'react'
const LeftPanel = (): JSX.Element => {
	const { users, clickStatus } = useContext(TableContext)
	const newTotalStatus = users.filter(
		(item: IRandomForm) => item.status === 'new',
	)

	return (
		<div className='left-panel blue-skin'>
			{/* <!-- Logo --> */}
			<LogoLeftPanel />

			{/* <!-- User -->    */}
			<UserAvatar />

			{/* <!-- Navigation 1 --> */}

			<div className='left-panel__navigation'>
				<div className='left-panel__navigation-title'>Заявки</div>
				<ul>
					{userStatus.map((value) => (
						<li key={Guid.raw()}>
							<a
								data-value={value}
								data-role='left-status'
								href='#'
								onClick={(e: MouseEvent<HTMLElement>) => {
									clickStatus(value as string)
								}}
								className={`${value === 'all' ? 'active' : ''}`}>
								{translate.status[value]}
								{value === 'new' && (
									<div className='badge' id='badge-new'>
										{newTotalStatus.length > 0 ? newTotalStatus.length : 0}
									</div>
								)}
							</a>
						</li>
					))}
				</ul>
			</div>

			{/* Navigation 1   */}
		</div>
	)
}

export default LeftPanel
