import { Guid } from 'guid-typescript'
import React, { MouseEvent, useContext, useState } from 'react'
import IProps from '../../Home/IProps'
import translate from '../../../helpers/translate'

const StatusLinkDiv = ({
	value,
	clickStatus,

	isActive,
	index,
}: IProps): JSX.Element => {
	return (
		<div
			key={Guid.raw()}
			className={`btn btn-light ${isActive ? 'active' : ''}`}
			data-value={value}
			data-index={index}
			onClick={(e: MouseEvent<HTMLElement>) => {
				clickStatus(value as string)
			}}>
			{translate.status[value]}
		</div>
	)
}

export default StatusLinkDiv
