import React from 'react'
import IRandomForm from '../../Home/IRandomForm'
import translate from '../../../helpers/translate'
import { Link } from 'react-router-dom'

type TypeUserItem = {
	value: IRandomForm
}
const UserItem = ({ value }: TypeUserItem) => {
	return (
		<>
			<tr>
				<th scope='row'> {value.id} </th>
				<td> {value.date} </td>
				<td> {(translate.type as any)[value.typeProduct]} </td>
				<td> {value.First_Name} </td>
				<td>{value.email}</td>
				<td>{value.telephone}</td>
				<td>
					<div
						className={`badge badge-pill ${
							value.status === 'new'
								? 'badge-danger'
								: value.status === 'inwork'
								? 'badge-warning'
								: value.status === 'complete'
								? 'badge-success'
								: ''
						}`}>
						{(translate.status as any)[value.status]}
					</div>
				</td>
				<td>
					<Link to={`/users/edit/${value.id}`}>Редактировать</Link>
				</td>
			</tr>
		</>
	)
}

export default UserItem
