import React, { ChangeEvent, useContext } from 'react'
import Button from '../../UI/Buttons/Button'
import { AppContext } from '../../App/App'
const Form = (): JSX.Element => {
	const { setFormFields, sendForm, ...randomForms } = useContext(AppContext)

	return (
		<form onSubmit={(e: ChangeEvent<HTMLFormElement>) => sendForm(e)}>
			<label>Ваши данные:</label>
			<div className='form-group'>
				<input
					id='name'
					type='text'
					name='name'
					className='form-control'
					placeholder='Имя и Фамилия'
					value={randomForms.First_Name}
					onChange={(e: ChangeEvent<HTMLInputElement>) => {
						setFormFields(e.target.name, e.target.value)
					}}
					required
				/>
			</div>
			<div className='form-group'>
				<input
					id='phone'
					type='text'
					name='phone'
					className='form-control'
					placeholder='Телефон'
					value={randomForms.telephone}
					onChange={(e: ChangeEvent<HTMLInputElement>) => {
						setFormFields(e.target.name, e.target.value)
					}}
				/>
			</div>
			<div className='form-group'>
				<input
					id='email'
					type='email'
					name='email'
					className='form-control'
					placeholder='Email'
					value={randomForms.email}
					onChange={(e: ChangeEvent<HTMLInputElement>) => {
						setFormFields(e.target.name, e.target.value)
					}}
					required
				/>
			</div>
			<div className='form-group'>
				<label htmlFor='exampleFormControlSelect1'>Продукт:</label>
				<select
					id='product'
					name='product'
					className='form-control'
					value={randomForms.typeProduct}
					onChange={(e: ChangeEvent<HTMLSelectElement>) => {
						setFormFields(e.target.name, e.target.value)
					}}>
					<option value='course-html'>Курс по верстке</option>
					<option value='course-js'>Курс по JavaScript</option>
					<option value='course-vue'>Курс по VUE JS</option>
					<option value='course-php'>Курс по PHP</option>
					<option value='course-wordpress'>Курс по WordPress</option>
				</select>
			</div>
			<div className='form-group'>
				<Button className='btn btn-lg btn-primary btn-block'>
					Оформить заявку
				</Button>
			</div>
		</form>
	)
}

export default Form
