import React, {
	MouseEvent,
	createContext,
	useContext,
	useEffect,
	useState,
} from 'react'
import LeftPanel from './LeftPanel/LeftPanel'
import MainPanel from './MainPanel/MainPanel'
import IRandomForm from '../Home/IRandomForm'
import IProps from '../Home/IProps'
import { AppContext, init } from '../../App/App'
export const TableContext = createContext<IProps>(init)
const TableForms = (): JSX.Element => {
	// меняем класс body при переходе на страницу
	useEffect(() => {
		document.body.classList.remove('radial-bg', 'flex-center')
		document.body.classList.add('body--dashboard')
	}, [])
	const { users, isLoading, errorFetch } = useContext(AppContext)
	const [flag, setFlag] = useState<boolean>(false)

	//    создаём localStorage если не найден
	// ======== Состояния для фильтров статус и тип продукта ===========

	const [status, setStatus] = useState<string>('')

	const [typeProduct, setProductType] = useState<string>('')

	// ==================================================================
	if (!localStorage.getItem('filter')) {
		localStorage.setItem('filter', '')
	}
	useEffect(() => {
		if (localStorage.getItem('filter')) {
			const setLocalStorage = JSON.parse(localStorage.getItem('filter'))
			setStatus(setLocalStorage.statusFilter)
			setProductType(setLocalStorage.productType)
		}
	}, [])

	// устанавливаем состояние для пользователей

	const [usersForFilters, setUsersForFilters] = useState<IRandomForm[]>(users)

	useEffect(() => {
		setUsersForFilters(users)
	}, [users, flag])

	// =============================================================

	//#region Фильтры пользователей

	const clickStatus = (valueStatus: string) => {
		setStatus(valueStatus)
	}

	//======= Фильтрация пользователей по статусу, по select  ==============
	useEffect(() => {
		// Если выбран фильтр статус : новый, в работе и тд.

		if (status !== '' && typeProduct === '') {
			setUsersForFilters(users.filter((item) => item.status === status))
		}
		// Если выбран фильтр тип продукта : курс по wordPress и тд.
		if (status === '' && typeProduct !== '') {
			setUsersForFilters(
				users.filter((item) => item.typeProduct === typeProduct),
			)
		}

		// Если выбраны оба фильтра, то ищем по параметрам обоих фильтров
		if (status !== '' && typeProduct !== '') {
			setUsersForFilters(
				users.filter(
					(item) => item.status === status && item.typeProduct === typeProduct,
				),
			)
		}

		// записываем данные в localStorage
		if (status !== '' || typeProduct !== '') {
			localStorage.setItem(
				'filter',
				JSON.stringify({
					statusFilter: status === '' ? 'all' : status,
					productType: typeProduct === '' ? 'all' : typeProduct,
				}),
			)
		}

		// ==============================================================
		if (status === 'all') {
			setStatus('')
			setFlag(!flag)
		}
		if (typeProduct === 'all') {
			setProductType('')
			setFlag(!flag)
		}

		if (status === 'all' && typeProduct === 'all') {
			setFlag(!flag)
			setStatus('')
			setProductType('')
		}
	}, [status, typeProduct])

	const clickProductType = (valueType: string) => {
		setProductType(valueType)
	}

	// ==============================================================

	//#endregion
	const [isActive, setIsActive] = useState<boolean>(false)
	//

	const toggleClass = (e: MouseEvent<HTMLElement>, index: number) => {}

	return (
		<>
			<TableContext.Provider
				value={{
					users,
					isLoading,
					errorFetch,
					usersForFilters,
					clickStatus,
					clickProductType,
					status,
					typeProduct,
					toggleClass,
					isActive,
				}}>
				<LeftPanel />
				<MainPanel />
			</TableContext.Provider>
		</>
	)
}

export default TableForms
